export const REST = Object.freeze({
    GET: 'GET',
    POST: 'POST',
    PUT: 'PUT',
    PATCH: 'PATCH',
    DELETE:'DELETE'
});


export async function customFetch(url,data = null,fetchType = REST.GET){

    const baseUrl = url;
    let fetchUrl = baseUrl;
    if(fetchType === REST.GET && data != null){
        const parameter = setQueryString(data);
        fetchUrl += `?${parameter}`;
    }

    try {
        const response = await fetch(fetchUrl, {
            method: fetchType,         // HTTP  (: GET)
            headers: {  // HTTP  
                "Accept": "*/*", // JSON  
                "Content-Type": fetchType === REST.GET ? "application/x-www-form-urlencoded" : "application/json"  // GET urlencoded 
            },
            body: fetchType === REST.PUT || fetchType === REST.POST ? JSON.stringify(data) : null,              //   (POST, PUT  )
            
            /*
            mode: "cors",          // CORS  
            credentials: "same-origin", //     
            cache: "default",      //    
            redirect: "follow",    //   
            referrer: "client",    //   (referrer) 
            referrerPolicy: "no-referrer-when-downgrade", // Referrer  
            integrity: "",         //   
            keepalive: false,      //      
            signal: AbortSignal,   //  (Abort)  
            */
        });
    
        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }

        //  JSON    
        const contentType = response.headers.get("content-type");

        if (contentType && contentType.includes("application/json")) {
            const resData = await response.json(); // JSON  
            console.log("JSON  :", resData);
            return resData;
        } else if (contentType && contentType.includes("text")) {
            const resData = await response.text(); //   
            console.log("  :", resData);
            return resData;
        } else {
            throw new Error("   .");
        }

    } catch (error) {
        console.error(" :", error); //  
    }
}


//util
function setQueryString(dicData){

    const queryString = new URLSearchParams(Object.entries(dicData)).toString();

    return queryString;
}
