
import React, { useState, useEffect } from "react";
import RoomType from "./RoomType";
import config from '../../../../config.js';

import '../css/RoomContent.css'

// RoomContent 
export default function RoomContent() {
  const [roomTypes, setRoomTypes] = useState([]);

  useEffect(() => {
    const fetchRoomTypes = async () => {
      try {
        const response = await fetch(`${config.API_BASE_URL}/api/users/rooms`, {
          method: 'GET',
          headers: {
            "Content-Type" : "application/json",
          },
          credentials: 'include',
        });
        const data = await response.json();
        setRoomTypes(data);
      } catch (error) {
        console.error("Error fetching room types:", error);
      }
    };

    fetchRoomTypes();
  }, []);

  return (
    <div className="RoomView_Top">
    <nav className="RoomView_container">
      <h2>RoomView</h2>
      <div className="room-content">
      <div className="room-header">
        <h1></h1>
        <p>      HJ       .<br/>
               ,                    .</p>
      </div>
      <div className="room-type-list">
        {/* roomTypes  map      RoomType   */}
        {roomTypes.map((roomType) => (
          <RoomType key={roomType.roomTypeId} roomType={roomType} />
        ))}
      </div>
    </div>
     
    </nav>
    </div>

  );
}