import { Link } from 'react-router-dom';
import React, { useState, useEffect } from 'react';
import './css/Footer.css';

export default function Footer() {
  const [userInfo, setUserInfo] = useState(null); //   

  //   
  const fetchUserInfo = async () => {
    try {
      const response = await fetch("http://localhost:8080/api/users/info", {
        method: "GET",
        credentials: "include", // JWT  
      });
      if (response.ok) {
        const data = await response.json();
        setUserInfo(data); //  
        return data; //   
      } else {
        console.error("   .");
        return null;
      }
    } catch (error) {
      console.error(" :", error);
      return null;
    }
  };

  //  
  const handleLogout = async () => {
    try {
      const response = await fetch("http://localhost:8080/api/users/logout", {
        method: "GET",
        credentials: "include", //   
      });

      if (response.ok) {
        setUserInfo(null); //  
        // alert(".");
        window.location.href = "/"; //  
      } else {
        console.error(" ");
      }
    } catch (error) {
      console.error(" :", error);
    }
  };

  //      
  useEffect(() => {
    fetchUserInfo();
  }, []);

  return (
      <div id="Footer">
        <div className="Footer_container">
          <h2>
            <Link to="/main">StarellaHotel</Link>
          </h2>
          <ul className="conection_foot">
            <h3>CONTACT US</h3>
            <li> 1544-8082</li>
            <li>  004- 21-0848-651</li>
            <li> @HJ Hotel </li>
          </ul>
          <ul className="location_foot">
            <h4>GROUP INFORMAYION</h4>
            <p>   1 2   1007</p>
            <p>
                : 130-29-4839 |  2001    : 
              2030- -9228  : StarellaHotel  :StarGroup
            </p>
            <p>
               : 1544-0834  ; 0505-200-0606  :
              StarellaHotel@howep.co.kr
            </p>
            <p>@StarellaHotel - sample page </p>
          </ul>
        </div>
        <p className="horizon"></p>
        <div className="copyright_foot">
        <span className="admin">
          <Link to="/admin">Admin</Link>
        </span>
          <span className="login">
          {userInfo ? (
              <>
                <span>{userInfo.name} </span> {/*    */}
                <button onClick={handleLogout} className="logout-button">
                  
                </button>
              </>
          ) : (
              <Link to="/users/login">Login</Link> //   
          )}
        </span>
          <span>StarellaHotel</span>
          <div className="reservation_box">
            <Link to="/reservation/guide">Reservation Now</Link>
          </div>
        </div>
      </div>
  );
}
