import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom'; //    useNavigate  
import ButtonEx from '../../../common/ButtonEx';
import '../css/MemberTermsPage.css';

function MemberTermsPage() {
    const navigate = useNavigate(); //    

    // 24.11.12 [ ] :      state
    const [terms, setTerms] = useState({
        all: false,        //   
        service: false,
        privacy: false,
        marketing: false,
    });

    // 24.11.12 [ ] :   
    const handleCheckboxChange = (e) => {
        const { name, checked } = e.target;
        setTerms((prevTerms) => {
            const newTerms = { ...prevTerms, [name]: checked };

            // ' '       
            if (name === 'all') {
                return {
                    all: checked,
                    service: checked,
                    privacy: checked,
                    marketing: checked,
                };
            } else {
                //       ' ' 
                newTerms.all = newTerms.service && newTerms.privacy && newTerms.marketing;
            }
            return newTerms;
        });
    };

    // 24.11.12 [] :      
    const isAllChecked = terms.service && terms.privacy;

    //     
    const handleNext = () => {
        if (isAllChecked) {
            alert("  .");
            navigate("/users/register"); //  
        } else {
            alert("        .");
        }
    };

    return (
        <div className="terms_page">
            <h2></h2>
            <p>       .</p>

            <div className="terms_checkbox">
                <label>
                    <input
                        type="checkbox"
                        name="all"
                        checked={terms.all}
                        onChange={handleCheckboxChange}
                    />
                     
                </label>
            </div>

            <div className="terms_checkbox">
                <label>
                    <input
                        type="checkbox"
                        name="service"
                        checked={terms.service}
                        onChange={handleCheckboxChange}
                    />
                        ()
                </label>
            </div>

            <div className="terms_checkbox">
                <label>
                    <input
                        type="checkbox"
                        name="privacy"
                        checked={terms.privacy}
                        onChange={handleCheckboxChange}
                    />
                         ()
                </label>
            </div>

            <div className="terms_checkbox">
                <label>
                    <input
                        type="checkbox"
                        name="marketing"
                        checked={terms.marketing}
                        onChange={handleCheckboxChange}
                    />
                        ()
                </label>
            </div>

            <ButtonEx
                id="terms_agree_button"
                action={handleNext} //     
                disabled={!isAllChecked} //      
            >
                
            </ButtonEx>
        </div>
    );
}

export default MemberTermsPage;
