import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import config from '../../../../config.js';

import '../css/MemberRegister.css';

const MemberRegister = () => {
    const [formData, setFormData] = useState({
        userId: '',
        email: '',
        password: '',
        confirmPassword: '',
        name: '',
        phone: '',
    });

    const [errorMessage, setErrorMessage] = useState('');
    const [successMessage, setSuccessMessage] = useState('');

    const navigate = useNavigate();

    //  
    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value,
        });
    };

    //   
    const handleSubmit = async (e) => {
        e.preventDefault();

        // , ,   
        const userIdRegex = /^[a-zA-Z0-9]{5,20}$/;
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        const phoneRegex = /^01[016789]-?\d{3,4}-?\d{4}$/;

        if (!userIdRegex.test(formData.userId)) {
            setErrorMessage(' 5~20    .');
            return;
        }
        if (!emailRegex.test(formData.email)) {
            setErrorMessage('   .');
            return;
        }
        if (!phoneRegex.test(formData.phone)) {
            setErrorMessage('   . (: 010-1234-5678)');
            return;
        }
        if (formData.password !== formData.confirmPassword) {
            setErrorMessage('  .');
            return;
        }

        //  
        try {
            const response = await fetch(`${config.API_BASE_URL}/api/users/register`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    userId: formData.userId,
                    email: formData.email,
                    password: formData.password,
                    name: formData.name,
                    phone: formData.phone,
                }),
            });

            if (!response.ok) {
                throw new Error(' .');
            }

            // JSON   
            const result = await response.json();
            setSuccessMessage(result.message); //   
            setErrorMessage('');

            //  
            navigate(`/users/signupcom?name=${formData.name}&userId=${formData.userId}`);
        } catch (error) {
            setErrorMessage(error.message || '   .');
        }
    };

    return (
        <form onSubmit={handleSubmit} className="register-form">
            <h2></h2>

            {errorMessage && <p className="error-message">{errorMessage}</p>}
            {successMessage && <p className="success-message">{successMessage}</p>}

            <div className="form-group">
                <label htmlFor="userId">*</label>
                <input
                    type="text"
                    id="userId"
                    name="userId"
                    placeholder="  "
                    value={formData.userId}
                    onChange={handleChange}
                    required
                />
            </div>

            <div className="form-group">
                <label htmlFor="email">*</label>
                <input
                    type="email"
                    id="email"
                    name="email"
                    placeholder="  "
                    value={formData.email}
                    onChange={handleChange}
                    required
                />
            </div>

            <div className="form-group">
                <label htmlFor="password">*</label>
                <input
                    type="password"
                    id="password"
                    name="password"
                    placeholder=" "
                    value={formData.password}
                    onChange={handleChange}
                    required
                />
                <input
                    type="password"
                    id="confirmPassword"
                    name="confirmPassword"
                    placeholder="  "
                    value={formData.confirmPassword}
                    onChange={handleChange}
                    required
                />
            </div>

            <div className="form-group">
                <label htmlFor="name">*</label>
                <input
                    type="text"
                    id="name"
                    name="name"
                    placeholder=" "
                    value={formData.name}
                    onChange={handleChange}
                    required
                />
            </div>

            <div className="form-group">
                <label htmlFor="phone">*</label>
                <input
                    type="tel"
                    id="phone"
                    name="phone"
                    placeholder=" "
                    value={formData.phone}
                    onChange={handleChange}
                    required
                />
            </div>

            <button id="registerSubmit" type="submit" className="custom-button">
                 
            </button>
        </form>
    );
};

export default MemberRegister;
