import { toDate, setDay } from "date-fns";
import { Link, useNavigate, useOutletContext } from "react-router-dom";
import CustomCalendar from "./CustomCalendar";
import { useEffect, useState } from "react";
import { customFetch, REST } from "../../../../util/customFetch";
import config from '../../../../config.js';

function ReservationForm(){

    const{
        roomTypeDataList, 
        reservationData,
        selectedYearAndMonth,
        selectedTimestamp, 
        setSelectedTimestamp // function
    } = useOutletContext();

    const [reservationPeriodList,setReservationPeriodList] = useState(new Array(roomTypeDataList.length));

    const [selectedRoom,setSelectedRoom] = useState(0);

    const [occupancy,setOccupancy] = useState(
        {
            adult : 0,
            kid : 0
        }
    );

    const navi = useNavigate();

    useEffect(()=>{
        const initList = [...reservationPeriodList];
        for(let i = 0; i< initList.length; i++){
            initList[i] = 1;
        }
        setReservationPeriodList(initList);
    },[roomTypeDataList])

    function onClickSelectDay(timestamp){
        setSelectedTimestamp(timestamp);
    }

    // 24.12.06  [] :               
    //                          -         ?    ?
    //                        .                                   
    function renderReservePeriodList(){
        const renderResult = [];
        for(let i = 0; i < 6; i++){
            if(i !== 0)
                renderResult.push(<option value={i+1}>{i+1}{i+2}</option>)
            else{    
                renderResult.push(<option selected value={i+1}>{i+1}{i+2}</option>);
            }
        }

        return renderResult;
    }

    function renderOccupancyOptionList(roomTypeData, bAdult){

        const renderResult = [];
        for(let i = 0; i<roomTypeData.maxOccupancy; i++)
            renderResult.push(
                bAdult ? 
                    roomTypeData.baseOccupancy !== i+1 ? <option value={i+1}>{i+1}</option> : <option selected value={i+1}>{i+1}</option> :
                    i !== 0 ? <option value={i}>{i}</option> : <option selected value={i}>{i}</option>
            );

        return renderResult;
    }

    function onChangePeriod(period, index){
        const list = [...reservationPeriodList];
        list[index] = period.target.value;
        setReservationPeriodList(list);
    }

    function onCheckedRoom(index){
        setSelectedRoom(index);
    }

    // public static class Create{
    //     public int memberId;
    //     public int roomId;
    //     public Timestamp checkIn;
    //     public Timestamp checkOut;
    //     public int totalAmount;
    // }
    function onClickReservation(){
        const checkInTimestamp = selectedTimestamp;
        const checkOutTimestamp = toDate(selectedTimestamp, { timeZone: 'Asia/Seoul' });
        setDay(checkOutTimestamp, checkOutTimestamp.getDay() + reservationPeriodList[selectedRoom]);
        const data = {
            memberId : 1,
            roomId : 3,
            checkIn : checkInTimestamp,
            checkOut : checkOutTimestamp.getTime(),
            totalAmount : reservationPeriodList[selectedRoom]*roomTypeDataList[selectedRoom].basePrice
        };

        console.log(data);
        const fetchReservation = async () => {
            const res = await customFetch(`${config.API_BASE_URL}/api/users/reservation`,data,REST.POST);

            // if(res !== null){
            //     console.log(res.message);
            //     console.log(res.reservationId);
            // }

            navi("/payment",{state:{key:{resReservationId : res.reservationId}}});
        }
        
        fetchReservation();
    }

    
    return(
        <>
            {/*   /   */}
            <div className="row row-cols-2">
                <div>
                    <CustomCalendar
                        year={selectedYearAndMonth.year} 
                        month={selectedYearAndMonth.month-1} 
                        customContent={(timestamp)=>{}} 
                        onCellClick={(timestamp)=>onClickSelectDay(timestamp)}
                    />    
                </div>
                <div>
                    <div>
                        <h1>{
                            ` ${selectedYearAndMonth.year} ${selectedYearAndMonth.month} 
                            ${toDate(selectedTimestamp, { timeZone: 'Asia/Seoul' }).getDate()}`
                        }</h1>
                    </div>
                    <div>
                        <p><b></b> 1544-6062</p>
                        <p><b></b>  19     .</p>
                    </div>
                </div>               
            </div>
            
            <div>
                {/*   */}
                <div>
                    <div><h4><span>    .</span></h4></div>
                    <div>
                        {roomTypeDataList.map((item,index)=>(
                                <div className="p-3 m-2 row border" key={index}>
                                    <div className="col-8">
                                        <div>{`${item.name} / ${item.description}`}</div>
                                    
                                        <div className="row">
                                            <div className="col">
                                                <div className="input-group mb-3">
                                                    <label className="input-group-text" for="reserve_period_select"></label>
                                                    <select className="form-select" id="reserve_period_select" onChange={(e)=>onChangePeriod(e,index)}>
                                                        {renderReservePeriodList()}
                                                    </select>
                                                </div>
                                            </div>
                                            <div className="col">
                                                <div className="input-group mb-3">
                                                    <label className="input-group-text" for="occupancy_adult_select"></label>
                                                    <select className="form-select" id="occupancy_adult_select">
                                                        {renderOccupancyOptionList(item,true)}
                                                    </select>
                                                </div>
                                            </div>
                                            <div className="col">
                                                <div className="input-group mb-3">
                                                    <label className="input-group-text" for="occupancy_kid_select"></label>
                                                    <select className="form-select" id="occupancy_kid_select">
                                                        {renderOccupancyOptionList(item, false)}
                                                    </select>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-4 row justify-content-center">
                                        <div className="col-4">
                                            <p></p>
                                            <h2>{reservationPeriodList[index]*item.basePrice}</h2>
                                        </div>
                                        <div className="col-4">
                                            <div class="form-check">
                                                <input class="form-check-input" type="radio" name="select_room" id="selectRoom" value={index} checked={selectedRoom === index} onChange={()=>onCheckedRoom(index)}/>   
                                                <label class="form-check-label" htmlFor="selectRoom">
                                                   
                                                </label>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )
                        )}
                    </div>
                </div>
                {/*   /    */}
                <div className="row row-cols-2">
                    <div>
                        <h4> </h4>
                        <div>
                            <div className="row row-cols-2">
                                <div className="col">
                                    <input type="text" name="wr_name" required placeholder="  ."/>
                                </div>
                                <div className="col">
                                    <input type="text" name="sh_phone" required placeholder=" "/>
                                </div>
                                <div className="col">
                                    {/* ...? */}
                                </div>
                                <div className="col">
                                    <Link to="">  </Link>
                                    <span>
                                        <label htmlFor="all_agr"> </label>
                                        <input type="checkbox" id="all_agr"/>
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div>
                        <h4> </h4>
                        <div>
                            <div>
                                
                                <p><span>{reservationPeriodList[selectedRoom]*roomTypeDataList[selectedRoom].basePrice}</span></p>
                            </div>
                            <div>
                                <p><b></b>{`    1544-6062`}</p>
                                <p><b></b>{`      24  (${setDay(
                                                    toDate(selectedTimestamp, { timeZone: 'Asia/Seoul' }), 
                                                    toDate(selectedTimestamp, { timeZone: 'Asia/Seoul' }).getDay() + 1)})`
                                                }
                                </p>
                                <p>
                                            . <br />
                                         . <br />
                                          .
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
                {/*  /   */}
                <div className="container px-5">
                    <div className="row gx-5 justify-content-center">
                        <Link to="/reservation/check_out" className="col-2 p-3 m-3 btn bg-light border"></Link>
                        <div className="col-2 p-3 m-3 btn bg-light border" onClick={onClickReservation}></div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default ReservationForm;