import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import '../../member/css/MemberLoginPage.css';
import { useUserInfo } from '../../../common/UserInfoProvider';
import config from '../../../../config.js';

function MemberLoginPage() {
    const [formData, setFormData] = useState({ userId: '', password: '' }); //  
    const [errorMessage, setErrorMessage] = useState(''); //  

    const navigate = useNavigate();
    const { fetchUserInfo } = useUserInfo(); //  

    //   
    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({ ...formData, [name]: value });
    };

    //     
    const handleLogin = async (e) => {
        e.preventDefault(); //    
        try {
            const response = await fetch(`${config.API_BASE_URL}/api/users/login`, {
                method: 'POST',
                headers: { "Content-Type": "application/json" },
                credentials: "include",
                body: JSON.stringify(formData),
            });

            if (response.ok) {
                await fetchUserInfo(); //     
                window.location.href = "/";
            } else {
                const errorText = await response.text();
                setErrorMessage(errorText);
            }
        } catch (error) {
            setErrorMessage('    .');
        }
    };

    //       ( )
    useEffect(() => {
        fetchUserInfo();
    }, []); //      

    return (
        <div className="login-page">
            <h1></h1>
            <p>      .</p>
            <p>//  !!!!</p>

            {errorMessage && <p style={{ color: 'red' }}>{errorMessage}</p>}

            {/*    */}
            <form onSubmit={handleLogin} className="login-form">
                <input
                    type="text"
                    name="userId"
                    placeholder=" "
                    value={formData.userId}
                    onChange={handleChange}
                />
                <input
                    type="password"
                    name="password"
                    placeholder=" "
                    value={formData.password}
                    onChange={handleChange}
                />
                <button
                    id="login-button"
                    type="submit" //   submit 
                    className="custom-login-button"
                >
                    
                </button>
            </form>

            <div className="login-options">
                <p>
                    <button onClick={() => alert(' x')} className="btn-link"> </button>
                    {' / '}
                    <button onClick={() => alert(' x')} className="btn-link"> </button>
                    {' / '}
                    <button onClick={() => navigate('/users/terms')} className="btn-link"></button>
                </p>
            </div>
        </div>
    );
}

export default MemberLoginPage;
