import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import ButtonEx from '../../../common/ButtonEx';
import '../css/MemberSignUpComplete.css';
import headImage from '../../../../assets/images/head.png';

function MemberSignUpComplete() {
    const location = useLocation();
    const navigate = useNavigate(); //    useNavigate  

    // URL    
    const searchParams = new URLSearchParams(location.search);
    const name = searchParams.get('name');   // ?name=
    const userId = searchParams.get('userId'); // ?userId=

    //   
    const handleHomeClick = () => {
        navigate('/'); // '/'  
    };

    return (
        <div className="signup-complete-page">
            <h1> </h1>
            <div className="signup-steps">
                <span>1  </span>
                <span>2   </span>
                <span>3  </span>
            </div>

            <div className="rewards-card">
                <img src={headImage} alt=" " />
                <div className="user-info">
                    <p>: {name || ' '}</p>
                    <p>: {userId || ' '}</p>
                </div>
            </div>

            <p className="special-offer">    </p>
            <div className="buttons">
                {/*    */}
                <ButtonEx id="home-button" action={handleHomeClick}> </ButtonEx>
            </div>
        </div>
    );
}

export default MemberSignUpComplete;
